.medical-advisors {
    // background:$-stoneGray-45;
    // height: 100vh;

    .tab {
        margin: 51px 0px;

        @include mq($until: desktop) {
            @include justify-flex-start;
            // justify-content: flex-start;
            overflow: auto;
        }


        button {
            border: 1.5px solid #282832;
            border-radius: 37px;
            padding: 8px 17px;
            margin-right: 12px;
            font-size: 14px;
            white-space: nowrap;

            &.active {
                background-color: #282832;
                color: #E2DED9;
            }

            &:hover {
                background-color: #282832;
                color: #E2DED9;
            }
        }
    }

    .medical-content {
        @include flex;
        @include align-center;
        // display: flex;
        // align-items: center;
        @include justify-center;
        margin-bottom: 40px;
        // justify-content: center;

        @include mq($until: desktop) {
            display: block;
        }



        .tab-detail {
            font-family: aeonik;
            font-size: 20px;
            max-width: 776px;
            width: 100%;
        }

        .number-round {
            max-width: 76px;
            width: 100%;
            height: 76px;
            /* left: 266px; */
            /* top: 2713px; */
            @include flex;
            @include align-center;
            @include justify-flex-end;
            background: #96F0FF;
            border-radius: 50%;
            // align-items: center;
            // display: flex;
            // justify-content: end;
            font-size: 66px;
            margin-right: 51px;

            @include mq($until: desktop) {
                margin: 0% auto;
            }
        }

    }

    .title {
        width: 598px;
        text-align: center;
        margin: 0% auto;

        @include mq($until: mobileLarge) {
            width: auto;
            font-size: 36px;
            line-height: 46px;
        }
    }



    .medical-box {
        @include flex;
        @include flex-wrap;
        // display: flex;
        // flex-wrap: wrap;

        .box {
            width: 33.3%;
            padding: 10px;

            @include mq($until: desktop) {
                width: 100%;
            }

            .advisors {
                background: #fff;
                border-radius: 10px;
                padding: 20px 17px 22px 17px;

                .medical-head {
                    font-size: 26px;
                    font-family: 'Aeonik';
                    margin-bottom: 20px;
                }

                .medical-detail {
                    width: 296px;
                    font-family: AEONIK;
                    font-size: 18px;
                    line-height: 21px;
                    padding-bottom: 28px;

                    @include mq($until: mobileLarge) {
                        width: auto;
                    }
                }


                .medical-date {
                    margin-top: 18px;
                    font-family: 'Aeonik';
                }


                .remote {
                    border-radius: 37px;
                    border: 1px solid;
                    text-align: center;
                    font-family: 'Aeonik';
                    width: 83px;
                    padding: 2px;
                    height: 30px;
                    margin: 18px 0px;
                }

            }
        }
    }

    .roles-title {
        text-align: center;
        font-size: 20px;
        color: $-primary-60;
        font-family: aeonik;
    }
}