.our-clinicians {
    height: 100vh;
    @include align-center;
    @include flex;
    @include justify-center;
    // align-items: center;
    // display: flex;
    // justify-content: center;
    background: $-stoneGray-20;
    overflow: hidden;

    @include mq($until: desktop) {
        height: auto;
    }

    .title {
        font-size: 56px;
        line-height: 72px;
        text-align: center;
        color: $-primary;
        font-family: 'Simula';
        margin-bottom: 48px;

        @include mq($until: desktop) {
            font-size: 36px;
            line-height: 46px;
        }
    }

    .clinicians {
        .slick-dots {
            @include flex;
            @include justify-center;
            // display: flex;
            // justify-content: center;
            // margin: 0;
            padding: 1rem 0;
            list-style-type: none;
            margin-top: 24px;

            li {
                margin: 0 0.25rem;
            }

            button {
                display: block;
                width: 30px;
                height: 10px;
                padding: 0;
                border: none;
                border-radius: 20px;
                background-color: $-stoneGray-45;
                text-indent: -9999px;
                cursor: pointer;
            }

            li.slick-active button {
                background-color: $-secondary;
            }

        }
    }

    .clinicians-slider {
        // display: flex;
        border-radius: 10px;
        background: #E2E8EB;
        padding: 40px 0px 0px 37px;
        position: relative;

        @include mq($until: desktop) {
            display: inline-block;
            padding: 36px 36px 0px 36px;
        }

        .doctor-detail {
            width: 321px;
            font-family: 'Aeonik';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 21px;
            color: $-primary;

            .doctor-name {
                font-family: 'CircularStd-Medium';
                line-height: 24px;
                margin-top: 39px;
            }

            .lead {
                font-size: 12px;
                line-height: 18px;
            }

            .reg {
                font-size: 10px;
                margin-top: 10px;
                margin-bottom: 25px;
            }
        }

        img {
            &.do-img {
                position: absolute;
                right: 0%;
                width: 285px;
                bottom: 0%;

                @include mq($until: desktop) {
                    position: relative;
                    right: 0%;
                    width: auto;
                    bottom: 0%;
                }
            }
        }
    }

    .text {
        margin: 25px 0px 0px 37px;
        cursor: pointer;
    }

}












