.empowering {
  @include flex;
  @include align-center;
  text-align: center;
  height: 100vh;

  // align-items: center;
  // display: flex;

  .join-drawer-open {
    background-color: #F3F2F0;
    width: 0px;
    right: 0%;
    position: absolute;
    top: 0%;
    height: 100vh;
    z-index: 9;
    opacity: 0;
    visibility: hidden;
    transform: translateX(100%);
    overflow: hidden;
    transition: 0.3s;

    .title-content {
      @include flex;
      // display: flex;
      @include justify-space-between;
      @include align-center;
      // justify-content: space-between;
      padding: 20px 20px 0px 44px;
      position: absolute;
      z-index: 999999999999;
      width: 100%;
      // align-items: center;

      .medical-title {
        font-size: 24px;
        line-height: 28px;
        color: #282832;
      }

      .close123 {
        cursor: pointer;
        font-size: 27px;
      }
    }
    .join-blue {
      position: absolute;
      width: 493.57px;
      height: 537px;
      background: radial-gradient(50% 83.84% at 0% 5.61%, #96F0FF 4.65%, rgba(226, 222, 217, 0) 88.85%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
      border-radius: 10px 10px 0px 0px;
      filter: blur(60px);
    }
  }

  

  &.open-join-drawer {
    .join-drawer-open {
      background-color: #F3F2F0;
      width: 500px;
      right: 0%;
      position: absolute;
      top: 0%;
      height: 100vh;
      z-index: 9999999;
      display: block;
      opacity: 1;
      visibility: visible;
      transform: translateX(0);
      // overflow: hidden;
      overflow: inherit;
      transition: 0.3s;

      @include mq($until: mobileLarge) {
        width: 100%;
      }
    }
  }

  .join-drawer-body{
    .main-title {
      font-size: 66px;
      line-height: 84px;
      text-align: center;
      font-family: 'Simula';
      margin-top: 70px;
    }

    .join-drawer-detail {
      width: 335px;
      font-size: 16px;
      line-height: 24px;
      text-align: center;
      color: #7E7E84;
      margin: 0% auto;
      font-family: 'Aeonik';
      margin-bottom: 58px;
    }

    form{
      input{
        &.join-form {
          width: 315px;
          height: 50px;
          background: #fff;
          border-radius: 25px;
          border: none;
          padding: 0px 20px;
          margin-bottom: 12px;
        }
      }

      textarea {
        width: 315px;
        background: #fff;
        border-radius: 10px;
        border: none;
        padding: 0px 20px;
        margin-bottom: 12px;
      }

      a{
        &.upload {
          @include flex;
          @include align-center;
          @include justify-center;
          width: 315px;
          height: 50px;
          // display: inline-block;
          border-radius: 10px;
          background: #C4C4C4;
          // align-items: center;
          // display: flex;
          margin: 0% auto;
          text-align: center;
          // justify-content: center;
        }

        &.btn{
          &.submit {
            width: 305px;
            height: 50px;
            border: 1px solid #282832;
            border-radius: 25px;
            margin-top: 45px;
          }
        }
      }
    }
  }

  // background: $-stoneGray-45;
  .small-text {
    font-size: 14px;
    font-family: AR;
    float: left;
  }

  .big-title {
    font-family: 'Simula';
    font-size: 66px;
    line-height: 84px;
    text-align: center;
    letter-spacing: -0.015em;
    color: #282832;
    width: 711px;
    margin: 51px auto;

    @include mq($until: desktop) {
      width: 100%;
    }
  }
}


