.accordion-container {
    @include flex;
    @include flex-wrap;
    width: 100%;

    .accordion-block {
        // width: 25%;

        @include mq($until: desktop) {
            width: 100%;
        }

        .submenu-title {
            @include flex;
            @include justify-space-between;
            @include align-center;
            pointer-events: none;
            padding: 18px 0;
            font-size: 14px;
        
            &:after {
                display: none;
        
                @include mq($until: desktop) {
                    content: "";
                    width: 10px;
                    height: 10px;
                    border: 2px solid $-turquoise-50;
                    border-top-width: 0;
                    border-left-width: 0;
                    transform: scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(45deg) translate3d(0px, 0px, 0px) skew(0deg, 0deg);
                    display: block;
                }
            }        
            &.t-open {
                &:after {
                    transform: scale3d(1, 1, 1) rotateX(180deg) rotateY(0deg) rotateZ(45deg) translate3d(0px, 0px, 0px) skew(0deg, 0deg);
                }
            }
        
            @include mq($until: desktop) {
                pointer-events: all;
                padding: 15px 0;
            }
        }

        .custom-accordion-content {
            display: block;

            @include mq($until: desktop) {
                display: none;
            }
        }
    }
}