header {
    position: absolute;
    width: 100%;

    .main-header {
        position: relative;
        padding: 30px;
        align-items: center;
        @include flex;
        // display: flex;
        @include justify-space-between;
        // justify-content: space-between;
        // background-color:$-stoneGray-45;
        // z-index: 999999;

        .header-left {
            @include flex;
            float: left;
            // display: flex;
            @include align-center;
            z-index: 999999;

            .menu {
                margin-right: 10px;

                span {
                    cursor: pointer;
                    font-size: 28px;
                }
            }
        }

        .header-middle {
            float: none;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            @include mq($until: desktopLarge) {
                display: none;
            }

            a {
                float: left;
                color: $-primary;
                text-align: center;
                padding: 14px 16px;
                text-decoration: none;
                font-size: 16px;
            }

            ul {
                @include flex;
                list-style: none;
                padding: 0;
            }
        }

        .header-right {
            float: right;

            @include mq($until: desktopLarge) {
                display: none;
            }

            a {
                margin-right: 10px;

                &.get {
                    height: 34px;
                    width: 108px;
                    border-radius: 6px;
                    border: 1.5px solid #4AD0C0;
                    padding: 12px;
                }
            }
        }

        #drawer-open {
            position: absolute;
            background-color: #fff;
            top: 85px;
            right: 0;
            visibility: hidden;
            opacity: 0;
            width: 100%;
            transition: all 0.3s ease-in-out;
            transform: translateY(-100%);

            @include mq($until: desktop) {
                width: 325px;
                height: 100vh;
                top: 0;
                transform: translateX(100%);
            }

            ul {
                &.header-menu {
                    list-style: none;
                    margin: 0px;
                    padding: 0px;

                    li {
                        // padding: 2px 0;
                        margin-bottom: 5px;
                        font-size: 14px;

                        a {
                            color: $-primary-dark;
                            text-decoration: none;
                        }
                    }
                }
            }

        }
    }

    &.open {
        .sub-menu {
            position: absolute;
            right: 0;
            top: 0;
            overflow: inherit;
            width: 100%;
            height: 100vh;
            // background-color: #fff;
            transform: translateX(0);

            #drawer-open {
                visibility: visible;
                opacity: 1;
                z-index: 99;
                // transform: translateX(0);
            }
        }

    }

    .sub-menu {
        position: absolute;
        right: 0;
        top: 0;
        overflow: hidden;
        width: 100%;
        height: auto;
        // background-color: #fff;
        transform: translateX(0);

        #drawer-open {
            visibility: hidden;
            opacity: 0;
            transform: translateX(0);

            span {
                &.close {
                    float: right;
                    padding: 20px;
                    cursor: pointer;
                }
            }
        }
    }

}