.big-title {
  font-family: 'Simula';
  font-size: 66px;
  line-height: 84px;
  text-align: center;
  letter-spacing: -0.015em;
  color: #282832;
  width: 711px;
  margin: 0% auto;
}

.small-text {
  font-size: 14px;
  font-family: AR;
}


.accordion-container .accordion-block .submenu-title.why:after {
  content: "";
  width: 10px;
  height: 10px;
  border: 2px solid $-primary;
  border-top-width: 0;
  border-left-width: 0;
  transform: scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(-45deg) translate3d(0px, 0px, 0px) skew(0deg, 0deg);
  display: block;
}

.accordion-container .accordion-block .submenu-title.why.t-open:after {
  transform: scale3d(1, 1, 1) rotateX(180deg) rotateY(0deg) rotateZ(225deg) translate3d(0px, 0px, 0px) skew(0deg, 0deg);
}