.join-us {
    @include flex;
    @include align-center;
    height: 100vh;
    text-align: center;

    @include mq($until: desktop) {
        // width: 325px;
        margin: 0% auto;
    }

    @include mq($until: desktop) {
        .title{
            font-size: 36px;
        }
    }
    

    .join-detail {
        text-align: center;
        font-family: 'Aeonik';
        font-size: 20px;
        color: $-primary-60;
        margin-top: 45px;

        .mail {
            color: $-primary;
            margin-top: 30px;
        }
    }

    .contact {
        @include flex;
        @include justify-center;
        @include flex-wrap;
        // display: flex;
        margin: 65px auto;
        // justify-content: center;
        // flex-wrap: wrap;

        a {
            &.btn {
                width: 305px;
                height: 51px;
                margin: 13px;
            }
        }
    }


}