.treat {
    // background:$-stoneGray-45;
    height: 100vh;
    align-items: center;
    display: flex;

    .treated {
        display: flex;
        flex-wrap: wrap;
        align-items: center;

        .why-treated-details {
            width: 50%;

            // img {
            //     position: absolute;

            //     @include mq($until: desktop) {
            //         position: relative;
            //         margin: 0% auto;
            //     }
            // }

            @include mq($until: desktop) {
                width: 100%;
                position: relative;
            }

            .title {
                font-size: 56px;
                line-height: 72px;
                letter-spacing: -0.015em;
                color: $-primary;
                font-family: 'Simula';
            }

            .provide {
                /* font-weight: 700; */
                font-size: 20px;
                line-height: 28px;
                font-family: 'AR';
                color: $-primary;
            }

            .recruitment {
                width: 100%;
                display: block;

                .accordion-block {
                    border-bottom: 1px solid #7E7E84;
                }

                .submenu-title {
                    pointer-events: all;
                    cursor: pointer;
                }

                .custom-accordion-content {
                    display: none;
                }
            }

        }
    }
}